import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue'
import { cloneObj } from '@/utils/tools.js'
import { api } from '@/config/common.js'
import { query } from '@/api/common.js'
export default {
	components: {
		uniSearchBar,
		uniList,
		uniListItem,
		uniDrawer
	},
	data() {
		return {
			title: '盘点',
			productCategory: null,
			datas: null,
			searchDatas: null,
			searchKey: '',
			curSelectType: '00000',
			typeMenu: false,
			productList: []
		}
	},
	onLoad() {
		this.productCategory = uni.getStorageSync('productCategory').productCategories
		this.productCategory.unshift({ productcategoryid: '00000', productcategoryname: '所有分类' })
		let reqData = {
			productid: 0,
			productcategory: '',
			brand: '',
			pageIndex: 1,
			pageRows: -1
		}
		this.$refs.loading.open()
		query(api.stkStock, reqData).then(res => {
			this.$refs.loading.close()
			if (res.status == 200 && res.data.returnCode == '0000') {
				this.datas = res.data.data.resultList
				for (let item of this.datas) {
					this.$set(item, 'checked', false)
				}
				this.searchDatas = this.datas
			} else {
				uni.showToast({
					icon: 'none',
					title: res.data.returnMessage
				})
			}
		}).catch(error => {
			this.$refs.loading.close()
			uni.showToast({
				icon: 'none',
				title: error
			})
		})
	},
	computed: {
		headerHeight() {
			return this.$headerIsSearchHeight
		},
		mainHeight() {
			return this.$mainIsSearchHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleSelectType(val) {
			this.curSelectType = val.productcategoryid
			if (val.productcategoryid !== '00000') {
				this.searchDatas = this.datas.filter((item) => {
					return item.productcategory == val.productcategoryname
				})
			} else {
				this.searchDatas = this.datas
			}
			this.handleTypeMenuClose()
		},
		handleClickItem(val) {
			if (!val.checked) {
				this.productList.push(val)
				val.checked = true
			} else {
				this.productList = this.productList.filter((item) => {
					return item.productid !== val.productid
				})
				val.checked = false
			}
		},
		handleSearch(val) {
			if (val.value) {
				this.searchDatas = this.datas.filter((item) => {
					if (!item.productname) {
						item.productname = ''
					}
					return item.productname.indexOf(val.value) !== -1
				})
			} else {
				this.searchDatas = this.datas
			}
		},
		handleTypeMenuOpen() {
			this.typeMenu = !this.typeMenu
		},
		handleTypeMenuClose() {
			this.typeMenu = false
		},
		handleSelectAll() {
			this.productList = []
			for (let item of this.searchDatas) {
				item.checked = true
				this.productList.push(item)
			}
		},
		handleNoneSelectAll() {
			this.productList = []
			for (let item of this.searchDatas) {
				item.checked = false
			}
		},
		handleNext() {
			uni.navigateTo({
				url: './add/add?productList=' + JSON.stringify(this.productList)
			})
		}
	}
}
